*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
}

a[href^="#"] {
  margin: 5rem;
  letter-spacing: .2rem;
  font-size: 3rem;
}

a[href^="#"]:link, a[href^="#"]:visited {
  color: yellow;
  text-decoration: none;
}
